<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .sf-pre-goods-img-w {
    width: 110px;
    height: 60px;
    position: relative;
    cursor: pointer;
  }
  .sf-pre-goods-img-w>img {
    width: 110px;
    height: 60px;
  }
  .sf-pre-goods-img-w>.vendor-tag {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background: rgba(248, 242, 242, 0.5);
    color: #222222;
    font-size: 12px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="申请时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRangeReq"
            @datePickerRangeChange="defaultRangeReqChange">
          </date-picker-range>
        </el-form-item>
        <el-form-item label="审核时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRangeCheck"
            @datePickerRangeChange="defaultRangeCheckChange">
          </date-picker-range>
        </el-form-item>
        <el-form-item label="审核状态">
          <el-select class="search-input" v-model="searchContent.checkStatus" filterable placeholder="请选择审核状态" size="small" @change="doSearchHandel(true)" clearable>
            <el-option
              v-for="item in checkStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动商品ID">
          <el-input class="search-input" v-model.trim="searchContent.activityPlatCityGoodsId" placeholder="活动商品ID" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.goodsName" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="供应商专供">
          <el-select class="search-input" v-model="searchContent.vendorNo" filterable placeholder="供应商专供" size="small" @change="doSearchHandel(true)" clearable>
            <el-option
              v-for="item in vendorList"
              :key="item.vendorNo"
              :label="item.vendorName"
              :value="item.vendorNo">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="活动状态">
          <el-select class="search-input" v-model="searchContent.activityStatus" filterable placeholder="请选择状态" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          <el-button size="mini" @click="exportData" icon="el-icon-download" :loading="exportLoading">导出</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-circle-check" :disabled="checkedList.length === 0 || checkLoading" @click="checkHandel(false, checkedList)">批量审核</el-button>
      </div>
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          type="selection"
          align="center">
        </el-table-column>
        <el-table-column min-width="110px" label="申请时间">
          <template slot-scope="scope">
            <div v-if="scope.row.reqTime">
              <p>{{scope.row.reqTime.substr(0, 10)}}</p>
              <p>{{scope.row.reqTime.substr(11, 8)}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="140px" label="ID">
          <template slot-scope="scope">
            <div>
              <p class="ll-erp-p-no">活动ID: {{scope.row.activityId}}</p>
              <p class="ll-erp-p-no">活动商品ID: {{scope.row.activityPlatCityGoodsId}}</p>
              <p class="ll-erp-p-no">SKU编码: {{scope.row.skuNo}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="280px" label="商品信息">
          <template slot-scope="scope">
            <div class="ll-p-m-w">
              <div class="sf-pre-goods-img-w">
                <img v-if="scope.row.presaleImageUrl && scope.row.presaleImageUrl != ''" :src="scope.row.presaleImageUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
                <div v-if="scope.row.vendorName" class="vendor-tag">{{scope.row.vendorName}}专供</div>
              </div>
              <div class="ll-p-m-c-r">
                <div>{{scope.row.activityGoodsName || '-'}}</div>
                <div class="ll-erp-p-no">商品编码：{{scope.row.goodsNo || '--'}}</div>
                <div class="ll-erp-p-no">区域商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="120px" prop="activityStatus" label="活动状态" :formatter="activityStatusFormatter"></el-table-column>
        <el-table-column min-width="90px" prop="oldPrice" label="原售价"></el-table-column>
        <el-table-column min-width="90px" prop="newPrice" label="新售价"></el-table-column>
        <el-table-column min-width="90px" label="审核状态">
          <template slot-scope="scope">
            <div>{{scope.row.checkStatus === 1 ? '已审核' : scope.row.checkStatus === 0 ? '待审核' : ''}}</div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" label="审核时间">
          <template slot-scope="scope">
            <div v-if="scope.row.checkTime">
              <p>{{scope.row.checkTime.substr(0, 10)}}</p>
              <p>{{scope.row.checkTime.substr(11, 8)}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" prop="checkUser" label="审核人"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="80">
          <template slot-scope="scope" size="small">
            <el-button v-if="scope.row.checkStatus === 0" @click="checkHandel(true, scope.row)" type="text" size="mini" icon="el-icon-circle-check" :disabled="checkLoading">审核</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import config from "@/utils/config"
  import requestUrl from '@/api/Supplier'
  import Storage from '@/utils/localStorage'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import exportSheet from '@/utils/exportExcel'
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        loading: false,
        exportLoading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        tableData: [],
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          checkStatus: 0,
          activityPlatCityGoodsId: '',
          goodsName: '',
          vendorNo: '',
          activityStatus: '',
          defaultRangeReq: [`${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 00:00:00`, `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`],
          reqStartTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 00:00:00`,
          reqEndTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`,
          defaultRangeCheck: [],
          checkStartTime: '',
          checkEndTime: ''
        },
        vendorList: [],
        // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
        statusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '进行中',
            value: 3
          },
          {
            label: '未发布',
            value: 0
          },
          {
            label: '未开始',
            value: 2
          },
          {
            label: '已结束',
            value: 4
          },
          {
            label: '已终止',
            value: 5
          }
        ],
        checkStatusOptions: [
          {
            label: '待审核',
            value: 0
          },
          {
            label: '已审核',
            value: 1
          }
        ],
        checkedList: [],
        exportConfig: {
          skuNo: 'SKU编码',
          goodsNo: '商品编码',
          activityPlatCityGoodsId: '活动商品ID',
          activityGoodsName: '活动商品名称',
          spec: '规格',
          activityStatusStr: '活动状态',
          oldPrice: '原售价',
          newPrice: '新售价',
          checkStatusStr: '审核状态',
          checkTime: '审核时间',
          checkUser: '审核人'
        },
        checkLoading: false
      }
    },
    mounted() {
      this.getVendorlist()
      this.doSearchHandel(true)
    },
    components: {
      "date-picker-range": DatePickerRange
    },
    methods: {
      getVendorlist() {
        this.$http.post(requestUrl.vendorlist, {regionCode: this.searchContent.regionCode, DIFFCITY: this.searchContent.DIFFCITY}).then(res => {
          if(res.code === 0) {
            this.vendorList = res.data && res.data.length ? [{vendorNo: '', vendorName: '全部'}, ...res.data] : []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("获取供应商编码及名称（编码[vendorCode]，名称[vendorName]）报错：", e.message);
        })
      },
      defaultRangeReqChange(result) {
        this.searchContent.defaultRangeReq = result ? [...result] : result
        this.searchContent.reqStartTime = result ? result[0] : ''
        this.searchContent.reqEndTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      defaultRangeCheckChange(result) {
        this.searchContent.defaultRangeCheck = result ? [...result] : result
        this.searchContent.checkStartTime = result ? result[0] : ''
        this.searchContent.checkEndTime = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      handleSelect(checkedRows, row) {
        if(checkedRows.includes(row)) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {
            return item.storeNo === row.storeNo
          })
          this.checkedList.splice(index, 1)
        }
      },
      handleSelectAll(val) {
        this.checkedList = [...val]
      },
      doSearchHandel(params) {
        if(params === true) {
          this.pageNum = 1
        }
        this.loading = true
        this.checkStatusStr = []
        this.$http.post(requestUrl.quoteList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              this.tableData = res.data.result && res.data.result.length ? res.data.result : []
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      activityStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return "未发布"
        }else if(cellValue == 1) {
          return "已发布"
        }else if(cellValue == 2) {
          return "未开始[发布中]"
        }else if(cellValue == 3) {
          return "进行中[发布中]"
        }else if(cellValue == 4) {
          if(row.activityType === 11) {
            return "已结束[发布中]"
          }else {
            return "已结束"
          }
        }else if(cellValue == 5) {
          return "已终止"
        }else if(cellValue == 6) {
          return "已失效"
        }
      },
      exportData() {
        this.exportLoading = true
        this.$http.post(requestUrl.quoteExport, { ...this.searchContent }).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            let tempData = res.data && res.data.result && res.data.result.length ? res.data.result : []
            tempData.forEach(item => {
              if(item.activityStatus === 0) {
                item.activityStatusStr = "未发布"
              }else if(item.activityStatus == 1) {
                item.activityStatusStr = "已发布"
              }else if(item.activityStatus == 2) {
                item.activityStatusStr = "未开始[发布中]"
              }else if(item.activityStatus == 3) {
                item.activityStatusStr = "进行中[发布中]"
              }else if(item.activityStatus == 4) {
                if(item.activityType === 11) {
                  item.activityStatusStr = "已结束[发布中]"
                }else {
                  item.activityStatusStr = "已结束"
                }
              }else if(item.activityStatus == 5) {
                item.activityStatusStr = "已终止"
              }else if(item.activityStatus == 6) {
                item.activityStatusStr = "已失效"
              }
              item.checkStatusStr = item.checkStatus === 1 ? '已审核' : item.checkStatus === 0 ? '待审核' : ''
            })
            if(tempData.length === 0) {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              })
              this.isDisabled = false
            }else {
              const tHeader = utils.objectToArray(this.exportConfig, false)
              const filterVal = utils.objectToArray(this.exportConfig, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tempData,
                filename: `报价审核商品`
              })
              setTimeout(() => {
                this.$notify.success({
                  title: '温馨提示',
                  message: `报价审核商品导出成功`
                })
                this.isDisabled = false
              }, 500)
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [checkHandel 审核]
       * @param  {[type]} data [description]
       * @return {[type]}      [true  单个审核    false   批量审核]
       */
      checkHandel(type, data) {
        let params = {
          DIFFCITY: true
        }
        let idsList = []
        if(type) {
          idsList.push(data.id)
          params.ids = idsList
        }else {
          idsList = this.checkedList.map(item => {
            return item.id;
          })
          params.ids = idsList
        }
        this.$confirm('您确定要审核所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.checkLoading = true
          utils.showLoading()
          this.$http.post(requestUrl.checkPrice, {...params}).then(res => {
            this.checkLoading = false
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("审核成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.checkLoading = false
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      }
    }
    
  }
</script>


